<script>
import { mapState } from 'vuex';
import MaskList from '../../components/mask';
import NavBar from "../../components/NavBar";
import footBar from "../../components/foot";
import { handleClickEvent } from '../../util/util.js';
function IsMobile() {
    var userAgentInfo = navigator.userAgent;
    var Agents = ["Android", "iPhone",
                "SymbianOS", "Windows Phone",
                "iPad", "iPod"];
    var flag = false;
    for (var v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
            flag = true;
            break;
        }
    }
    return flag;
   }

export default {
  components: {
    NavBar,
    MaskList,
    footBar
  },
   computed: {
    ...mapState({
      isMask: state => state.isMask,
      isAndroid: state => state.isAndroid,
      isiOS: state => state.isiOS,
    }),
     i18n(){
          return this.$i18n.locale
      },
  },
  watch: {
    isMask: function(val) {
      const indexH = document.querySelector('.index');
      const mainH = document.documentElement.clientHeight;
      this.$nextTick(() => {
        indexH.style.height =val? mainH +'px' : 100 +'%';
        indexH.style.overflow = val? 'hidden' : 'visible';
      })
    },
     i18n(){
      if(IsMobile()){    /* 移动端 */
          if(this.$i18n.locale=='en'){
               this.changeHeight = true
               this.mobileEnHieight = true
           }else{
               this.changeHeight = false
               this.mobileEnHieight = false
            }
         }
      }  
  },
  data(){
    return{
      img1Url:require('../../assets/icon/home_btn_normal.png'),
      imgTpgUrl:require('../../assets/img/project_image_mobile_1.png'),
      imgTseUrl:require('../../assets/img/project_image_tse.png'),
      imgLiyingUrl:require('../../assets/img/project_image_liying.png'),
      imgPingtaiUrl:require('../../assets/img/project_image_pingtai.png'),
      forIe9:{
          change1:false,
          change2:false,
          change3:false,
          change4:false,
      },
      forIeTextChangeBiger:false,
      p2forIE:false,
      changeHeight:false,
      ie11:false,
      moblieImg:false,
      mobileEnHieight:''
    }
  },
  methods:{
     stopPropatation($event){
        // var get= $event.stopPropatation || $event.cancelBubble  
           if(!IsMobile()){
               $event.stopPropagation();
           }
    },
    go(path){
      if(path=='projectTpg'){
            this.handleClickEvent('82096','click_project_TPG')
        if(IsMobile()){
            // this.handleClickEvent('82096','click_project_TPG')
        }
        this.$router.push('/projectTpg')
        }

      if(path=='projectTse'){
         if(!IsMobile()){
             this.handleClickEvent('82097','click_project_TSE')
        }
        this.$router.push('/projectTse')
        }

      if(path=='projectLiYing'){
         this.handleClickEvent('82098','click_project_liying')
         if(IsMobile()){
        // this. handleClickEvent('82098','click_project_liying')
        }
        this.$router.push('/projectLiYing')
        }

      if(path=='projectAudioAndVideo'){
          if(!IsMobile()){
            this.handleClickEvent('82099','click_project_platform')
        }
        this.$router.push('/projectAudioAndVideo')
        }
    },
     handleClickEvent(id, eventName, params) {
      handleClickEvent(id, eventName, params)
    },
  },
  mounted(){
      if(IsMobile()){
        console.log("手机端")
        this.$refs.mobileTouch1.addEventListener("touchstart",()=>{
           this.$refs.mobileTouch1.style.border = "1px solid rgba(50,130,255,1)"
           this.$refs.m1.style.background  = "rgba(50,130,255,0.1)"
    })
        
    this.$refs.mobileTouch2.addEventListener("touchstart",()=>{
           this.$refs.mobileTouch2.style.border = "1px solid rgba(50,130,255,1)"
           this.$refs.m2.style.background  = "rgba(50,130,255,0.1)"
    })
    this.$refs.mobileTouch3.addEventListener("touchstart",()=>{
           this.$refs.mobileTouch3.style.border = "1px solid rgba(50,130,255,1)"
           this.$refs.m3.style.background  = "rgba(50,130,255,0.1)"
    })
    this.$refs.mobileTouch4.addEventListener("touchstart",()=>{
           this.$refs.mobileTouch4.style.border = "1px solid rgba(50,130,255,1)"
           this.$refs.m4.style.background  = "rgba(50,130,255,0.1)"
    })
     
      this.$refs.mobileTouch2.addEventListener("touchend",()=>{
           this.$refs.mobileTouch2.style.background = null
           this.$refs.mobileTouch2.style.border = null
            this.$refs.m2.style.background  = "none"    
    })
     this.$refs.mobileTouch1.addEventListener("touchend",()=>{
           this.$refs.mobileTouch1.style.background = null
           this.$refs.mobileTouch1.style.border = null
            this.$refs.m1.style.background  = "none"    
    })
      this.$refs.mobileTouch3.addEventListener("touchend",()=>{
           this.$refs.mobileTouch3.style.background = null
           this.$refs.mobileTouch3.style.border = null
            this.$refs.m3.style.background  = "none"    
    })
      this.$refs.mobileTouch4.addEventListener("touchend",()=>{
           this.$refs.mobileTouch4.style.background = null
           this.$refs.mobileTouch4.style.border = null
            this.$refs.m4.style.background  = "none"    
    })
     
    }






    if(navigator.appName == "Microsoft Internet Explorer"&&parseInt(navigator.appVersion.split(";")[1].replace(/[ ]/g, "").replace("MSIE","")) == 9){
        // ("您的浏览器版本IE9")
        this.forIe9.change1 = true
        this.forIe9.change2 = true
        this.forIe9.change3 = true
        this.forIe9.change4 = true

    }
    
     if(navigator.appName == "Microsoft Internet Explorer"&&parseInt(navigator.appVersion.split(";")[1].replace(/[ ]/g, "").replace("MSIE","")) == 10){
        // ("您的浏览器版本IE10")
        // ie10不支持border-image,用ie9的类，图片边框（内部一侧高亮）和hover时图片高亮边框，
        this.forIe9.change3 = true
        this.forIe9.change4 = true
    }
     if(navigator.userAgent.indexOf(("rv:11.0")) >  -1){
        // ("您的浏览器版本IE11")
        this.ie11 = true
    }
     if (!!window.ActiveXObject || "ActiveXObject" in window){
        // ("您的浏览器版本IE")
         
         this.forIeTextChangeBiger = true
          this.p2forIE = true
     }

     if(IsMobile()){    /* 移动端 */
          if(this.$i18n.locale=='en'){
               this.changeHeight = true
               this.moblieImg = true
           }else{
               this.changeHeight = false
                this.moblieImg = true
            }
         }
  }
} 
</script>
<style  lang="scss" scoped >
@import '../../assets/css/project.scss';
